import React from "react";
import { Header } from "semantic-ui-react";
import "@fontsource/barlow-semi-condensed";

function Click(clicked) {
  var navbar = document.querySelector(".main-nav ul");
  if(clicked){
    navbar.classList.add("hidden")
    navbar.classList.remove("active")
    console.log(navbar.classList.toString())
  }
  else{
    navbar.classList.add("active");
    navbar.classList.remove("hidden")
    console.log(navbar.classList.toString())
  }
  
}

function Navbar() {
  const [clicked, setClicked] = React.useState(false);
  return (
    <header className="main-header">
      <a href="/" className="brand-logo">
        <Header textAlign="left" style={{color: "rgb(222, 222, 222)", fontFamily: 'Barlow Semi Condensed'}} as='h2'>
          ESLAM HASSAN SHAWKY
          <Header.Subheader style={{color: "#ada8a8", fontFamily: 'Barlow Semi Condensed'}}>
            PRODUCTION DESIGNER
          </Header.Subheader>
        </Header>
      </a>
      <div href="/" className="toggle-button" onClick={() => {Click(clicked); setClicked(!clicked);}}>
        <span className="bar"></span>
        <span className="bar"></span>
        <span className="bar"></span>
      </div>
      <nav className="main-nav">
        <ul className="main-ul">
          <li className="companymenuli">
            <a href="/" style={{pointerEvents: "none"}}>MY WORK</a>
            <ul className="submenuli">
                <li>
                  <a href="#/commercial">
                  COMMERCIAL
                  </a>
                </li>
                <li>
                <a href="#/film">
                  FILMS/SERIES
                </a>
                </li>
                <li>
                <a href="#/videoclips">
                  VIDEO CLIPS
                </a>
                </li>
            </ul>
          </li>
          <li>
            <a href="#/making">MAKING</a>
          </li>
          <li>
            <a href="#/contact">CONTACT</a>
          </li>
          <li>
            <a href="#/about">ABOUT</a>
          </li>
        </ul>
      </nav>
    </header>
  );
}

export default Navbar;