import React from "react";
import { projects } from "./imageimport";
import { Grid } from "semantic-ui-react";
import HoverImage from "../utilities/hoverImage";

export default function List(props) {
  const [open, setOpen] = React.useState(false);
  function handleClick(pro) {
    console.log(pro);
    console.log("Image clicked");
    setOpen(!open);
    props.handleModal(pro);
  }
  const listItems = projects.map((project) => (
    <Grid.Row>
      <Grid.Column width={"10"}>
        <div
          style={{
            margin: "auto",
            height: "400px",
            width: "80%",
            position: "relative",
            textAlign: "center",
          }}
          onClick={() => handleClick(project)}
        >
          <HoverImage normalImage={project.image} hoverImage={project.gif} />
        </div>
      </Grid.Column>
      <Grid.Column width={6}>
        <div
          style={{
            height: "400px",
            width: "80%",
            position: "relative",
            textAlign: "left",
          }}
        >
          <p
            style={{
              color: "white",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "50px",
            }}
          >
            {project.name}
          </p>

          <p
            style={{
              color: "grey",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "20px",
            }}
          >
            {project.description}
          </p>
        </div>
      </Grid.Column>
    </Grid.Row>
  ));
  return listItems;
}
