import image2 from "../../assets/Screenshot (26).png";
import image1 from "../../assets/Screenshot (29).png";
import gif2 from "../../assets/726det.gif";
import gif1 from "../../assets/726dra.gif";
import video1 from "../../assets/1.mp4";
import video2 from "../../assets/2.mp4";

function importAll(r) {
  return r.keys().map(r);
}

const images = importAll(
  require.context("../../assets/", false, /\.(png|jpe?g|svg)$/)
);

const projects = [];

console.log(images);

for (const i in Object.keys(images)) {
  projects.push({
    id: 0, // Used in JSX as a key
    name: "Vodafone",
    description: "This is a description for the project Vodafone",
    image: images[i],
    gif: gif1,
    video: video1,
  });
  console.log(i);
}

export default projects;
