import React, { useState, useEffect } from "react";
import { Container, Grid, Segment, Modal } from "semantic-ui-react";
import Navbar from "../utilities/navbar";
import "@fontsource/barlow-semi-condensed";
import useWindowDimensions from "../utilities/windowSize";

import List from "./projects";
import ListPhone from "./projectsPhone";

function Commercial() {
  const [colnum, setColnum] = useState(true);
  const [open, setOpen] = React.useState(false);
  const [project, setProject] = useState({});
  const { height, width } = useWindowDimensions();
  function handleModal(project) {
    console.log("here");
    setOpen(!open);
    setProject(project);
  }
  useEffect(() => {
    console.log(height, width);
    if (width < 800) {
      setColnum(false);
    } else {
      setColnum(true);
    }
  }, [width]);
  return (
    <Container
      fluid
      textAlign="center"
      style={{
        backgroundColor: "black",
      }}
    >
      <Navbar />
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <br></br>
      <div
        style={{
          margin: "auto",
          width: "80%",
          position: "relative",
          textAlign: "center",
          zIndex: "1",
        }}
      >
        <Segment style={{ backgroundColor: "transparent" }}>
          {colnum ? (
            <Grid divided columns={2}>
              <List handleModal={handleModal} />
            </Grid>
          ) : (
            <Grid>
              <ListPhone handleModal={handleModal} />
            </Grid>
          )}
        </Segment>
      </div>
      <Modal
        closeIcon
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}
        open={open}
        basic
      >
        <div
          style={{
            width: "80%",
            position: "relative",
            textAlign: "left",
          }}
        >
          <span
            style={{
              color: "white",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "50px",
              padding: "0px",
            }}
          >
            {project.name}
          </span>
          <br></br>
          <br></br>
          <span
            style={{
              color: "white",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "20px",
              padding: "0px",
            }}
          >
            {project.description}
          </span>
        </div>
        <Modal.Content image>
          <video src={project.video} width="750" height="500" controls />
        </Modal.Content>
        <Modal.Actions></Modal.Actions>
      </Modal>
    </Container>
  );
}

export default Commercial;
