import React, { Component } from "react";
import { Image } from "semantic-ui-react";
import images from "./imageimport";
import "react-responsive-carousel/lib/styles/carousel.min.css";

class ImageComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeImageIndex: 0,
      imgPath: images[0],
      counter: 0,
      visible: true,
    };
  }

  componentDidMount() {
    console.log("images");

    setInterval(() => {
      let newActiveIndex =
        this.state.activeImageIndex === 4 ? 0 : this.state.activeImageIndex + 1;
      this.setState({
        activeImageIndex: newActiveIndex,
      });
    }, 8000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <Image
        key={this.state.activeImageIndex}
        className={"elementToFadeInAndOut frontImage"}
        src={images[this.state.activeImageIndex]}
      />
    );
  }
}
export default ImageComponent;
