import "./App.css";
import React from "react";
import Helmet from "react-helmet";
import HomepageLayout from "./components/dashboard/dashboard";
import { HashRouter, Route, Routes } from "react-router-dom";
import Contact from "./components/contact/contact";
import About from "./components/about/about";
import Commercial from "./components/commercial/commercial";
import Film from "./components/films/films";
import Videos from "./components/videos/videos";
import Making from "./components/making/making";

function App() {
  return (
    <HashRouter>
      <Helmet bodyAttributes={{ style: "background-color : black" }} />
      <Routes>
        <Route exact path="/" element={<HomepageLayout />} />
        <Route exact path="/contact" element={<Contact />} />
        <Route exact path="/about" element={<About />} />
        <Route exact path="/commercial" element={<Commercial />} />
        <Route exact path="/film" element={<Film />} />
        <Route exact path="/videoclips" element={<Videos />} />
        <Route exact path="/making" element={<Making />} />
        <Route path="*" element={<HomepageLayout />} />
      </Routes>
    </HashRouter>
  );
}

export default App;
