import React from "react";
import { projects } from "./imageimport";
import { Grid } from "semantic-ui-react";
import HoverImagePhone from "../utilities/hoverImage";

export default function ListPhone(props) {
  const [open, setOpen] = React.useState(false);
  function handleClick(pro) {
    console.log(pro);
    console.log("Image clicked");
    setOpen(!open);
    props.handleModal(pro);
  }
  const listItems = projects.map((project) => (
    <Grid.Row>
      <div
        style={{
          margin: "auto",
          height: "400px",
          width: "80%",
          position: "relative",
          textAlign: "center",
        }}
        onClick={() => handleClick(project)}
      >
        <HoverImagePhone normalImage={project.image} hoverImage={project.gif} />
        <div
          style={{
            top: "250px",
            height: "10px",
            width: "80%",
            position: "relative",
            textAlign: "left",
          }}
        >
          <span
            style={{
              color: "white",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "50px",
              padding: "0px",
            }}
          >
            {project.name}
          </span>
          <br></br>
          <br></br>
          <span
            style={{
              color: "white",
              fontFamily: "Barlow Semi Condensed",
              fontSize: "20px",
              padding: "0px",
            }}
          >
            {project.description}
          </span>
        </div>
      </div>
    </Grid.Row>
  ));
  return listItems;
}
