import React from "react";
import { Image } from "semantic-ui-react";

export default function HoverImagePhone(props) {
  function over(e) {
    e.currentTarget.src = props.hoverImage;
  }
  function out(e) {
    e.currentTarget.src = props.normalImage;
  }
  return (
    <Image
      style={{
        height: "100%",
        position: "absolute",
        left: "0px",
        width: "100%",
        overflow: "hidden",
        objectFit: "cover",
      }}
      src={props.normalImage}
      onMouseOver={over}
      onMouseOut={out}
      onTouchStart={over}
      onTouchEnd={out}
      alt=""
    />
  );
}
